import Route from 'next/router';
import { setCookie } from 'nookies';
import { createContext, useState } from 'react';

import { api } from '@/services/api';

type MesageResponse = {
  status: boolean;
  mfa: boolean;
  message: string;
};

type SiginInData = {
  usuario: string;
  password: string;
};

type User = {
  name: string;
  usuario: string;
  avatar: string;
};

type AuthContextType = {
  isAuthenticated: boolean;
  user: User | null;
  siginIn: (data: SiginInData) => void;
  erro: MesageResponse;
};

export const AuthContext = createContext({} as AuthContextType);

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null);
  const [erro, setErro] = useState<MesageResponse>(null);

  const isAuthenticated = !!user;

  function siginIn({ usuario, password }: SiginInData) {
    api
      .post('/api/Usuario/Login', {
        usuario: usuario,
        senha: password,
      })
      .then((res) => {
        if (res.status == 200) {
          if (res.data.success && !res.data.data.duploFator) {

            setCookie(undefined, 'RDCUU', res?.data?.data?.token, {
              maxAge: 60 * 60 * 0.2,
            });

            setUser(res.data.usuario);
            Route.push('/credito/consulta');
          } else {
            if (res.data.data?.duploFator) {
              setErro({ status: false, mfa: true, message: null });
            } else {
              setErro({
                status: true,
                mfa: false,
                message: res.data.messages[0],
              });
            }
          }
        }
      })
      .catch((err) => {
        // tratamento de erro de request api
      });
  }

  return (
    <AuthContext.Provider value={{ user, isAuthenticated, siginIn, erro }}>
      {children}
    </AuthContext.Provider>
  );
}
